.Filter {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 0 0 0 15px;

  width: auto;

  &__label {
    font-size: 18px;
    font-style: italic;

    margin: 0;
    margin-bottom: 10px;
  }

  &__input {
    padding: 10px 20px;
    width: 400px;
  }
}
